import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { getProjects, createProject, getPendingInvitations, respondToInvitation, deleteProject } from '../services/api';
import './Dashboard.css';

function Dashboard({ user, onLogout }) {
  const [projects, setProjects] = useState([]);
  const [invitations, setInvitations] = useState([]);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [newProject, setNewProject] = useState({ name: '', description: '' });
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    try {
      const [projectsRes, invitationsRes] = await Promise.all([
        getProjects(),
        getPendingInvitations()
      ]);
      setProjects(projectsRes.data);
      setInvitations(invitationsRes.data);
    } catch (error) {
      console.error('  :', error);
    } finally {
      setLoading(false);
    }
  };

  const handleCreateProject = async (e) => {
    e.preventDefault();
    try {
      await createProject(newProject);
      setShowCreateModal(false);
      setNewProject({ name: '', description: '' });
      loadData();
    } catch (error) {
      console.error('  :', error);
    }
  };

  const handleDeleteProject = async (projectId, projectName, e) => {
    e.stopPropagation(); //        
    
    if (!window.confirm(` ,     "${projectName}"?    .`)) {
      return;
    }

    try {
      await deleteProject(projectId);
      loadData();
    } catch (error) {
      alert(error.response?.data?.error || '  ');
      console.error('  :', error);
    }
  };

  const handleInvitationResponse = async (projectId, action) => {
    try {
      await respondToInvitation(projectId, action);
      loadData();
    } catch (error) {
      console.error('   :', error);
    }
  };

  if (loading) {
    return <div className="loading">...</div>;
  }

  return (
    <div className="dashboard">
      <header className="dashboard-header">
        <h1>Task Manager</h1>
        <div className="user-info">
          <span>, {user?.name}!</span>
          <button onClick={onLogout} className="btn-secondary"></button>
        </div>
      </header>

      {invitations.length > 0 && (
        <div className="invitations-section">
          <h2></h2>
          <div className="invitations-list">
            {invitations.map(inv => (
              <div key={inv.id} className="invitation-card">
                <div>
                  <h3>{inv.name}</h3>
                  <p>: {inv.owner_name}</p>
                  <p className="invitation-desc">{inv.description}</p>
                </div>
                <div className="invitation-actions">
                  <button 
                    onClick={() => handleInvitationResponse(inv.id, 'approve')}
                    className="btn-success"
                  >
                    
                  </button>
                  <button 
                    onClick={() => handleInvitationResponse(inv.id, 'reject')}
                    className="btn-danger"
                  >
                    
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      <div className="projects-section">
        <div className="section-header">
          <h2> </h2>
          <button onClick={() => setShowCreateModal(true)} className="btn-primary">
            +  
          </button>
        </div>

        {projects.length === 0 ? (
          <div className="empty-state">
            <p>    </p>
            <p>  ,   </p>
          </div>
        ) : (
          <div className="projects-grid">
            {projects.map(project => (
              <div 
                key={project.id} 
                className="project-card"
                onClick={() => navigate(`/project/${project.id}`)}
              >
                <div className="project-card-header">
                  <h3>{project.name}</h3>
                  {project.role === 'owner' && (
                    <button
                      onClick={(e) => handleDeleteProject(project.id, project.name, e)}
                      className="btn-delete-project"
                      title=" "
                    >
                      
                    </button>
                  )}
                </div>
                <p>{project.description}</p>
                <div className="project-meta">
                  <span className={`role-badge ${project.role}`}>
                    {project.role === 'owner' ? '' : ''}
                  </span>
                  <span className="project-owner">
                    {project.owner_name}
                  </span>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {showCreateModal && (
        <div className="modal-overlay" onClick={() => setShowCreateModal(false)}>
          <div className="modal" onClick={(e) => e.stopPropagation()}>
            <h2>  </h2>
            <form onSubmit={handleCreateProject}>
              <div className="form-group">
                <label> </label>
                <input
                  type="text"
                  value={newProject.name}
                  onChange={(e) => setNewProject({ ...newProject, name: e.target.value })}
                  required
                  placeholder=""
                />
              </div>
              <div className="form-group">
                <label></label>
                <textarea
                  value={newProject.description}
                  onChange={(e) => setNewProject({ ...newProject, description: e.target.value })}
                  placeholder="  "
                  rows="3"
                />
              </div>
              <div className="modal-actions">
                <button type="button" onClick={() => setShowCreateModal(false)} className="btn-secondary">
                  
                </button>
                <button type="submit" className="btn-primary">
                  
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}

export default Dashboard;
